@import (less) "color.less";

@fontSizeTitle: 0.875em;

//elimina dei fastidiosi piedini 
a:focus, a:hover {
    text-decoration: none;
}
//--------------------------

.panel {
    border: 1px !important;
    border-color: @white;
    box-shadow: none;
    .panel-open {
        background-color: @lightest_gray !important;
        border: 1px !important;
        border-color: @gray;
    }
}

.panel-heading {
    border: 1px !important;
    box-shadow: none;
    color: @green;
    border-color: @gray;
    background-color: @white;
    padding: 1em 1.250em 1em 0em !important;
    height: 50px;
    &:hover {
        cursor: pointer;
    }

    h4 {
        margin: 0;
        font-size: 1em;
    }

    .panel-title {
        color: green;
        font-weight: bold;
        font-size: 0.750em !important;
        text-transform: uppercase;
    }
    .accordion-arrow-round {
        color: @black;
        position: relative;
        font-size: 30px;
        vertical-align: middle;
        margin-left:8px;
    }
}

.panel-body {
    margin-top:5px;
    border: 1px !important;
    box-shadow: none;
    background-color: @lightest_gray !important;
    border-radius: 0px 0px 2px 2px !important;
}

.panel-collapse {
    width: 100%;
    .panel-body;
    max-height: 0;
    transition: max-height 0.15s ease-out;
    overflow: hidden;
    &.in {
        max-height: 1000em;
        transition: max-height 0.25s ease-in;
    }
}

.panelTitle {
    font-size: @fontSizeTitle;
    font-weight: bold;
    //   text-transform: uppercse;
}

